/*Projects*/
    .projects-wrapper {
        display: flex;
        flex-wrap: wrap;
        width: 90%;
        margin: calc((98vh - 42vw) / 2) auto;
    }

/*ProjectsPopup*/
    .thumbnail {
        height: 20vw;
        width: 20vw;
        cursor: pointer;
        margin: 1vw 1vw;
        position: relative;
        overflow: hidden;
    }    

    .thumbnail img {
        width: 100%;
        height: 100%;
    }

    .popup-wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        z-index: 999;
        /*change this*/
        background-color: rgba(0,0,0,0.75);
        animation: fadeIn 0.25s;
    }

    .popup {
        height: 75vh;
        width: 50vw;
        background-color: var(--background);
        z-index: 1001;
        position: relative;
    }

    @keyframes fadeIn {
        0% {opacity: 0;}
        100% {opacity: 1;}
    }

    .img-container img {
        width: 100%;
    }

    .img-container {
        height: 70%;
        width: 100%;
        overflow: hidden;
    }

    .exit-btn {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 2000;
        border: none;
        background-color: transparent;
        padding: 0.5em 0.75em;
        cursor: pointer;
        background-color: var(--onBackground);
    }

    .exit-btn:active, .exit-btn:hover {
        background-color: var(--onHover);
        color: var(--background);
    }

    .popup h3 {
        display: inline-block;
    }

    .date {
        float: right;
    }

    .dark-hover {
        position: absolute;
        top: 0;
        left: 0;
        background-color: rgba(0,0,0,0.75);
        color: var(--background) !important;
        width: 100%;
        height: 100%;
        animation: fadeIn 0.25s;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    #projects a {
        text-decoration: none;
    }
    
    #projects a:hover {
        color: var(--onHover);
        text-decoration: underline;
    }